
.cont {
    display: flex;
  justify-content:space-between;
  align-items: center;
   /* padding-bottom: 5px;  */
   flex-direction: row; 
}
.box
{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: rgb(2, 2, 2);
    background-color: rgb(210 134 134);
    border: 2px solid rgb(242, 237, 237);
    border-radius: 30px;
    margin-right: 3px;
}
.center{
    display: flex;
    /* align-items: center; */
    justify-content: center; 
    color: rgb(22, 21, 21);
    /* background-color: aquamarine; */
}
.mar{
  padding-top: 1px;
  padding-bottom: 0px;
  margin-bottom: 0px;
}
.book{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40%;
    padding-left: 80px;
}
.book h2{
    margin: 1px;
    background-color:white;
    border-radius: 5px;
}
.box p{
    padding-left: 10px;
    color: rgb(13, 13, 16);
    margin: 0px;
}
.Next-page{
    background-color: aquamarine;
    /* background-image: url("next.jpg"); */
}
.box h2{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 200px;
    background-color: antiquewhite;
    border-radius: 4px;
    margin: 2px;
}

@media (max-width: 768px) {
    .cont {
        display: flex;
      justify-content:space-between;
      align-items: center;
       /* padding-bottom: 5px;  */
       flex-direction: column; 
    }

    .book{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        padding: 0px;
    }
}

