.contt {
  height: 520px;
  width: 100%;
  /* z-index: -1; */
}
.main {
  position: absolute;
  top:0;
  left:0;
  background-image: url("one.jpg");
  width: 100%;
  height: 470px;
  grid-column: 3/4;
  opacity: 1;
 
  z-index: -1;
  /* border-color: #1d1a1a; */
  border-width: 5px;
  background-size: 100% 100%;
  box-shadow: rgba(158, 154, 154, 0.09) 0px 2px 1px, rgba(125, 123, 123, 0.09) 0px 4px 2px,
    rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(117, 114, 114, 0.912) 0px 16px 8px,
    rgba(0, 0, 0, 0.09) 0px 32px 16px;
  animation: changeImg 20s linear infinite;
  /* border: 2px solid red; */
}
.text p {
  margin-right: 15px;
  margin-left: 24px;
  margin-top: 0px;
  margin-bottom: 0px;
}
.text {
  /* display: flex; */
  align-items: center;
  justify-content: center; 
  color: #0c0c0c;
  /* padding-bottom: 100px; */
  position: absolute;
  top: 290px;
  width: 100%;
  height: 312px;
  text-align: center;
  animation: change 20s linear infinite;
}
.text h1{
  margin: 0px;
}
.updates{
  width: 30%;
  height:205px;
  position: absolute;
  top: 390px;
  left: 30px;
  background-color: white;
}
.news{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e1be22;
  color: white;
  /* border-radius: 4px; */
  /* border:solid white 2px; */
  font-size: 20px;
}
  @keyframes change {
    0% {
      background: linear-gradient(to bottom, #33ccff 0%, #ff99cc 100%);
    }
    25% {
      background: linear-gradient(to bottom, #33ccff 0%, #ff66ff 100%);
    }
    50% {
      background: linear-gradient(to bottom, #33ccff 0%, #99ccff 100%);
    }
    75% {
      background: linear-gradient(to bottom, #66ffcc 0%, #ff0066 100%);
    }
    100% {
      background: linear-gradient(to bottom, #ff66ff 0%, #ccffcc 100%);

    }
  }
  

@keyframes changeImg {
  0% {
    background-image: url("one.jpg");
  }
  25% {
    background-image: url("two.jpg");
  }
  50% {
    background-image: url("three.jpg");
  }
  75% {
    background-image: url("four.jpg");
  }
  100% {
    background-image: url("one.jpg");
  }
}

@media (max-width: 768px) {
  .contt{
    height: 649px;
    display:block;
    align-items:center;
    justify-content: space-between;
  }
  .updates{
    width: 88%;
    height:207px;
    position: absolute;
    top: 516px;
    /* left: 30px; */
    background-color: white;

    border:1px solid rgb(51, 221, 212);
  }
   .text{
    z-index: -1;
    height: 458px;
   }
}
